@-webkit-keyframes slide {
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 1692px, 0);
            transform: translate3d(0, 1692px, 0); /* The image width */
  }
}

@keyframes slide {
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 1692px, 0);
            transform: translate3d(0, 1692px, 0); /* The image width */
  }
}


