.fadeIn {
    -webkit-animation: faderIn 1.5s forwards;
            animation: faderIn 1.5s forwards;
}

.fadeOut {
    -webkit-animation: faderOut .5s forwards;
            animation: faderOut .5s forwards;
}

.refresher {
    -webkit-animation: refresh 1.5s forwards;
            animation: refresh 1.5s forwards;
}

.refresher2 {
    -webkit-animation: refresh2 1s forwards;
            animation: refresh2 1s forwards;
}

@-webkit-keyframes faderIn {
    0%   { opacity: 0; }
    100% { opacity: 1;}
}

@keyframes faderIn {
    0%   { opacity: 0; }
    100% { opacity: 1;}
}

@-webkit-keyframes faderOut {
    0%   { opacity: 1; }
    100% { opacity: 0; }
}

@keyframes faderOut {
    0%   { opacity: 1; }
    100% { opacity: 0; }
}

@-webkit-keyframes refresh {
    0%   { opacity: 1; }
    25% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes refresh {
    0%   { opacity: 1; }
    25% { opacity: 0; }
    100% { opacity: 1; }
}

@-webkit-keyframes refresh2 {
    0%   { opacity: 1; }
    25% { opacity: 0; }
    100% { opacity: 1; }
}

@keyframes refresh2 {
    0%   { opacity: 1; }
    25% { opacity: 0; }
    100% { opacity: 1; }
}
